import { createAsyncThunk } from "@reduxjs/toolkit"
import Api from "../Api"
export const getAllUsers = createAsyncThunk("/admin/get", async (userData, { rejectWithValue, getState }) => {
    try {
        const { data } = await Api.get("/admin/users")
        return data.result
    } catch (error) {
        return rejectWithValue(error.response.data.message || error.message)
    }

})

export const addCourse = createAsyncThunk("/admin/course", async (courseData, { rejectWithValue, getState }) => {
    try {
        const { data } = await Api.post("/admin/course", courseData)
        return data
    } catch (error) {
        return rejectWithValue(error.response.data.message || error.message)
    }

})

export const getCourse = createAsyncThunk("/admin/course/get", async (courseData, { rejectWithValue, getState }) => {
    try {
        const { data } = await Api.get("/admin/course")
        return data.result
    } catch (error) {
        return rejectWithValue(error.response.data.message || error.message)
    }

})