import { createSlice } from "@reduxjs/toolkit"
import { bookCourseAction, getKeyAction, initiatePaymentAction, loginUser, register, verifyPaymentAction } from "./PublicAction"

const publicSlice = createSlice({
    name: "public",
    initialState: {
        info: JSON.parse(localStorage.getItem("info"))
    },
    reducers: {
        invalidate: (state, { payload }) => {
            state[payload] = null
        },
        logout: (state, { payload }) => {
            state.info = null
            localStorage.removeItem("info")
        },
        paymentVery: (state, { payload }) => {
            state.paymentVerify = payload
        }
    },
    extraReducers: builder => {
        builder
            .addCase(register.pending, (state, { payload }) => {
                state.loading = true
            })
            .addCase(register.fulfilled, (state, { payload }) => {
                state.loading = false
                state.registered = payload
            })
            .addCase(register.rejected, (state, { payload }) => {
                state.loading = false
                state.error = payload
            })


            .addCase(loginUser.pending, (state, { payload }) => {
                state.loading = true
            })
            .addCase(loginUser.fulfilled, (state, { payload }) => {
                state.loading = false
                state.info = payload
            })
            .addCase(loginUser.rejected, (state, { payload }) => {
                state.loading = false
                state.error = payload
            })

            .addCase(getKeyAction.pending, (state, { payload }) => {
                state.loading = true
            })
            .addCase(getKeyAction.fulfilled, (state, { payload }) => {
                state.loading = false
                state.keyPay = payload
            })
            .addCase(getKeyAction.rejected, (state, { payload }) => {
                state.loading = false
                state.error = payload
            })

            .addCase(initiatePaymentAction.pending, (state, { payload }) => {
                state.loading = true
            })
            .addCase(initiatePaymentAction.fulfilled, (state, { payload }) => {
                state.loading = false
                state.orderId = payload
            })
            .addCase(initiatePaymentAction.rejected, (state, { payload }) => {
                state.loading = false
                state.error = payload
            })

            .addCase(verifyPaymentAction.pending, (state, { payload }) => {
                state.loading = true
            })
            .addCase(verifyPaymentAction.fulfilled, (state, { payload }) => {
                state.loading = false
                state.paymentVerify = true
                state.orderId = null
            })
            .addCase(verifyPaymentAction.rejected, (state, { payload }) => {
                state.loading = false
                state.error = payload
                state.orderId = null
            })
    }
})

export const { invalidate, logout, paymentVery } = publicSlice.actions
export default publicSlice.reducer