import React from 'react'
import "../../assets/css/style.css"
import { Link } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { logout } from '../redux/public/PublicSlice'
const Navbar = () => {
    const { info } = useSelector(state => state.public)
    const dispatch = useDispatch()
    return <div>

        <div className="container-fluid bg-slate bg-slate text-light p-0">
            <div className="row gx-0 d-none d-lg-flex">
                <div className="col-lg-7 px-5 text-start">
                    <div className="h-100 d-inline-flex align-items-center me-4">
                        <small className="fa fa-map-marker-alt text-warning me-2"></small>
                        <small>123 Street, New York, USA</small>
                    </div>
                    <div className="h-100 d-inline-flex align-items-center">
                        <small className="far fa-clock text-warning me-2"></small>
                        <small>Mon - Fri : 09.00 AM - 09.00 PM</small>
                    </div>
                </div>
                <div className="col-lg-5 px-5 text-end">
                    <div className="h-100 d-inline-flex align-items-center me-4">
                        <small className="fa fa-phone-alt text-warning me-2"></small>
                        <small>+012 345 6789</small>
                    </div>
                    <div className="h-100 d-inline-flex align-items-center mx-n2">
                        <Link className="btn btn-square rounded-0 border-0 border-end border-secondary" href=""><i
                            className="fab fa-facebook-f text-warning"></i></Link>
                        <Link className="btn btn-square  rounded-0 border-0 border-end border-secondary" href=""><i
                            className="fab fa-twitter text-warning"></i></Link>
                        <Link className="btn btn-square  rounded-0 border-0 border-end border-secondary" href=""><i
                            className="fab fa-linkedin-in text-warning"></i></Link>
                        <Link className="btn btn-square  rounded-0" href=""><i className="fab fa-instagram text-warning"></i></Link>
                    </div>
                </div>
            </div>
        </div>

        <nav className="navbar navbar-expand-lg bg-white navbar-light p-0 m-0" >
            <Link to={"/"} className="navbar-brand d-flex align-items-center border-end px-4 px-lg-5">
                <h2 className="m-0 text-slate"><i className="fa fa-car text-warning me-2"></i>Drivin</h2>
            </Link>
            {
                info
                    ? <div className="dropdown">
                        <button className="btn btn-primary dropdown-toggle" type="button" id="dropdownMenuButton1" data-bs-toggle="dropdown" >
                            {info.name}
                        </button>
                        <ul className="dropdown-menu">
                            <li><Link className="dropdown-item" to={"/"}>Home</Link></li>
                            <li><button className="dropdown-item" onClick={e => dispatch(logout())} >Logout</button></li>
                        </ul>
                    </div>
                    : <Link to={"/login"}> <button className='bg-slate btn btn-dark btn-md familyC'>Login</button></Link>
            }

            <button type="button" className="navbar-toggler me-4" data-bs-toggle="collapse" data-bs-target="#navbarCollapse">
                <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarCollapse">
                <div className="navbar-nav ms-auto p-4 p-lg-0">
                    <Link to={"/"} className="nav-item nav-link active text-slate">Home</Link>
                    <Link to={"/about"} className="nav-item nav-link text-slate">About</Link>
                    <Link to={"/courses"} className="nav-item nav-link text-slate">Courses</Link>

                    <Link to={"/contact"} className="nav-item nav-link text-slate">Contact</Link>
                </div>
                <Link to={"/courses"} className="btn btn-warning py-4 text-light px-lg-5 d-none d-lg-block rounded-0">Get Started<i
                    className="fa fa-arrow-right ms-3"></i></Link>
            </div>
        </nav>
    </div>
}

export default Navbar