import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link, useNavigate } from 'react-router-dom'
import { register } from '../redux/public/PublicAction'

const Register = () => {
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const { loading, registered, error } = useSelector(state => state.public)
    const [registerData, setRegisterData] = useState({
        name: "",
        email: "",
        password: "",
    })
    const handleRegister = () => {
        dispatch(register(registerData))
    }

    useEffect(() => {
        if (registered) {
            navigate("/login")
        }
    }, [registered])
    if (loading) return <div className="spinner-border text-primary">Loading....</div>
    return <div className=''>
        <div className="container  my-0" >
            <div className="row d-flex justify-content-center align -items-center ">
                <div className="col-lg-12 col-xl-11 ">
                    {
                        error && <div className="alert alert-danger">
                            {error}
                        </div>
                    }
                    <div className="card text-black   border-0" >
                        <div className="card-body">
                            <div className="row justify-content-center">
                                <div className="col-md-10 col-lg-6 col-xl-5 order-2 order-lg-1">

                                    <p className="text-center h1 fw-bold mb-5 mx-1 mx-md-4 mt-4">Sign up</p>

                                    <form className="mx-1 mx-md-4">

                                        <div className="d-flex flex-row align-items-center mb-4">
                                            <i className="fas fa-user fa-lg me-3 fa-fw"></i>
                                            <div className="form-outline flex-fill mb-0">
                                                <input type="text" id="form3Example1c" className="form-control" value={registerData.name} onChange={e => setRegisterData({ ...registerData, name: e.target.value })} />
                                                <label className="form-label" for="form3Example1c">Your Name</label>
                                            </div>
                                        </div>

                                        <div className="d-flex flex-row align-items-center mb-4">
                                            <i className="fas fa-envelope fa-lg me-3 fa-fw"></i>
                                            <div className="form-outline flex-fill mb-0">
                                                <input type="email" id="form3Example3c" className="form-control" value={registerData.email} onChange={e => setRegisterData({ ...registerData, email: e.target.value })} />
                                                <label className="form-label" for="form3Example3c">Your Email</label>
                                            </div>
                                        </div>

                                        <div className="d-flex flex-row align-items-center mb-4">
                                            <i className="fas fa-lock fa-lg me-3 fa-fw"></i>
                                            <div className="form-outline flex-fill mb-0">
                                                <input type="password" id="form3Example4c" className="form-control" value={registerData.password} onChange={e => setRegisterData({ ...registerData, password: e.target.value })} />
                                                <label className="form-label" for="form3Example4c">Password</label>
                                            </div>
                                        </div>

                                        <div className="d-flex flex-row align-items-center mb-4">
                                            <i className="fas fa-key fa-lg me-3 fa-fw"></i>
                                            <div className="form-outline flex-fill mb-0">
                                                <input type="password" id="form3Example4cd" className="form-control" value={registerData.password} />
                                                <label className="form-label" for="form3Example4cd">Repeat your password</label>
                                            </div>
                                        </div>

                                        <div className="form-check d-flex justify-content-center mb-5">

                                            <label className="form-check-label" for="form2Example3">
                                                <Link to={"/login"}>already have account</Link>
                                            </label>
                                        </div>

                                        <div className="d-flex justify-content-center mx-4 mb-3 mb-lg-4">
                                            <button type="button" className="btn btn-primary btn-lg" onClick={handleRegister}>Register</button>
                                        </div>

                                    </form>

                                </div>
                                <div className="col-md-10 col-lg-6 col-xl-7 d-flex align-items-center order-1 order-lg-2">

                                    <img src="https://mdbcdn.b-cdn.net/img/Photos/new-templates/bootstrap-registration/draw1.webp"
                                        className="img-fluid" alt="Sample image" />

                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
}

export default Register