import React from 'react'
import Navbar from './public/components/Navbar'
import PublicLayout from './public/PublicLayout'
import Home from './public/pages/Home'
import { BrowserRouter, Route, Routes } from 'react-router-dom'
import About from './public/components/About'
import Courses from './public/components/Courses'
import Appointment from './public/components/Appointment'
import Contact from './public/components/Contact'
import BookingPage from './public/components/BookingPage'
import Login from './public/components/Login'
import Register from './public/components/Register'
import AdminLayout from './admin/AdminLayout'
import Dashboard from './admin/component/Dashboard'
import ViewUsers from './admin/component/ViewUsers'
import Protected from './admin/Protected'
import AddCourse from './admin/component/AddCourse'

const App = () => {
  return <>
    <BrowserRouter>
      <Routes>
        <Route path='/' element={<PublicLayout />}>
          <Route index element={<Home />} />
          <Route path='about' element={<About />} />
          <Route path='courses' element={<Courses />} />
          <Route path='appointment' element={<Appointment />} />
          <Route path='contact' element={<Contact />} />
          <Route path='booking/:id' element={<BookingPage />} />
          <Route path='login' element={<Login />} />
          <Route path='register' element={<Register />} />
        </Route>
        <Route path='/admin' element={<Protected compo={<AdminLayout />} />}>
          <Route index element={<Dashboard />} />
          <Route path='users' element={<ViewUsers />} />
          <Route path='addcourse' element={<AddCourse />} />
        </Route>
      </Routes>
    </BrowserRouter>
  </>
}

export default App