import React, { useEffect } from 'react'
import coursesOne from '../../assets/image/coursesOne.jpg'
import coursesTwo from '../../assets/image/coursesTwo.jpg'
import coursesThre from '../../assets/image/coursesThree.jpg'
import { Link } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { getCourse } from '../redux/admin/adminAction'
import Loader from './Loader'

const Courses = () => {
    const dispatch = useDispatch()
    const { info, loading } = useSelector(state => state.public)
    const { Courses } = useSelector(state => state.admin)


    useEffect(() => {
        dispatch(getCourse())
    }, [])

    if (loading) return <div style={{ marginLeft: 500 }}><h1><Loader /></h1></div>

    return <>
        <div className="container-xxl courses my-0 py-6 pb-0">
            <div className="container">
                <div className="text-center mx-auto mb-5 wow fadeInUp" data-wow-delay="0.1s" >
                    <h6 className="text-warning fw-bold text-uppercase mb-2">Tranding Courses</h6>
                    <h1 className="display-6 mb-4 fw-bold text-slate" style={{ fontFamily: 'unset' }}>Our Courses Upskill You <br /> With Driving Training</h1>
                </div>
                <div className="row g-4 justify-content-center">
                    {console.log(Courses)}
                    {
                        [] || Courses.length !== 0
                            ? <>
                                {
                                    Courses && Courses.map(item => <div className="col-lg-4 col-md-6 wow fadeInUp" data-wow-delay="0.1s">
                                        <div className="courses-item d-flex flex-column bg-white overflow-hidden h-100">
                                            <h5 className="mb-3 p-2 text-light text-center fs-3 w-100 bg-warning">{item.coursetype}</h5>
                                            <div className="text-center p-4 pt-0">
                                                <div className="d-inline-block  text-slate fontMid">{item.Coursefee}
                                                </div>

                                                <div className='d-flex justify-content-evenly '>
                                                    <p className=' text-slate rounded-3 p-1'><i className='bi bi-check-circle text-slate'></i>{item.vehicle}</p>
                                                    <p className=' text-slate rounded-3 p-1'><i className='bi bi-check-circle text-slate'></i> Wagonar</p>
                                                    <p className=' text-slate rounded-3 p-1'><i className='bi bi-check-circle text-slate'></i> santro</p>

                                                </div>
                                                <ol className="breadcrumb justify-content-center mb-0">
                                                    <Link to={info ? `/booking/${item._id}` : "/login"}> <button type="button" className="btn btn-dark rounded-0">Book Now</button></Link>
                                                </ol>
                                                <span className='text-secondary'>Thanks For Booking</span>
                                            </div>
                                            <div className="position-relative mt-auto">
                                                <img className="img-fluid" src={coursesOne} alt="" />
                                                <div className="courses-overlay">
                                                    <a className="btn btn-outline-warning text-light border-2" href="">Read More</a>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    )
                                }
                            </>
                            : <div className='text-center'>
                                <div className="row text-center">
                                    <div className="col-sm-5 offset-sm-4 text-center">
                                        <h1 style={{ borderBottom: "5px solid red" }}>No Courses Available</h1>
                                    </div>
                                </div>
                            </div>
                    }
                    <hr />
                    <div className="col-lg-8 my-6 mb-0 wow fadeInUp" data-wow-delay="0.1s">
                        <div className="bg-warning text-center p-5">
                            <h1 className="mb-4 familyC">Make Appointment</h1>
                            <form>
                                <div className="row g-3">
                                    <div className="col-sm-6">
                                        <div className="form-floating">
                                            <input type="text" className="form-control border-0 rounded-0" id="gname"
                                                placeholder="Gurdian Name" />
                                            <label for="gname">Your Name</label>
                                        </div>
                                    </div>
                                    <div className="col-sm-6">
                                        <div className="form-floating">
                                            <input type="email" className="form-control border-0 rounded-0" id="gmail"
                                                placeholder="Gurdian Email" />
                                            <label for="gmail">Your Email</label>
                                        </div>
                                    </div>
                                    <div className="col-sm-6">
                                        <div className="form-floating">
                                            <select className="form-select rounded-0">
                                                <option value="1">Demo</option>
                                                <option value="2">7 Days Plan</option>
                                                <option value="3">15 Days Plan</option>
                                            </select>
                                            <label for="cname">Courses Type</label>
                                        </div>
                                    </div>
                                    <div className="col-sm-6">
                                        <div className="form-floating">
                                            <input type="text" className="form-control border-0 rounded-0" id="cage"
                                                placeholder="Child Age" />
                                            <label for="cage">Description</label>
                                        </div>
                                    </div>

                                    <div className="col-12">
                                        <button className="btn btn-dark w-100 py-3 rounded-0" type="submit">Submit</button>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </>
}

export default Courses