import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link, useNavigate } from 'react-router-dom'
import { loginUser } from '../redux/public/PublicAction'
import { useEffect } from 'react'
import Loader from './Loader'

const Login = () => {
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const { loading, info, error } = useSelector(state => state.public)
    const [loginData, setLoginData] = useState({
        email: "",
        password: "",
    })
    const handleLogin = () => {
        dispatch(loginUser(loginData))
    }

    useEffect(() => {
        if (info) {
            if (info.role === "user") {
                navigate("/")
            }
            if (info.role === "admin") {
                navigate("/admin")
            }
        }
    }, [info])

    if (loading) return <div style={{ marginLeft: 500 }}><h1><Loader /></h1></div>
    return <>
        {
            error && <div className="alert alert-alert">
                {error}
            </div>
        }
        <section className="my-5">
            <div className="container-fluid h-custom">
                <div className="row d-flex justify-content-center align-items-center h-100">
                    <div className="col-md-9 col-lg-6 col-xl-5">
                        <img src="https://mdbcdn.b-cdn.net/img/Photos/new-templates/bootstrap-login-form/draw2.webp"
                            className="img-fluid" alt="Sample image" />
                    </div>
                    <div className="col-md-8 col-lg-6 col-xl-4 offset-xl-1">
                        <form>
                            <div className="d-flex flex-row align-items-center justify-content-center justify-content-lg-start">
                                <p className="lead fw-normal mb-0 me-3">Sign in with</p>
                                <button type="button" className="btn btn-primary btn-floating mx-1 w-25">
                                    <i className="fab fa-google "></i>
                                </button>
                                <button type="button" className="btn btn-primary btn-floating mx-1 w-25">
                                    <i className="fab fa-facebook "></i>
                                </button>


                            </div>

                            <div className="divider d-flex align-items-center my-4">
                                <p className="text-center fw-bold mx-3 mb-0">Or</p>
                            </div>

                            <div className="form-outline mb-4">
                                <input type="email" id="form3Example3" className="form-control form-control-lg"
                                    placeholder="Enter a valid email address" value={loginData.email} onChange={e => setLoginData({ ...loginData, email: e.target.value })} />
                                <label className="form-label" for="form3Example3">Email address</label>
                            </div>

                            <div className="form-outline mb-3">
                                <input type="password" id="form3Example4" className="form-control form-control-lg"
                                    placeholder="Enter password" value={loginData.password} onChange={e => setLoginData({ ...loginData, password: e.target.value })} />
                                <label className="form-label" for="form3Example4">Password</label>
                            </div>

                            <div className="d-flex justify-content-between align-items-center">
                                <div className="form-check mb-0">
                                    <input className="form-check-input me-2" type="checkbox" value="" id="form2Example3" />
                                    <label className="form-check-label" for="form2Example3">
                                        Remember me
                                    </label>
                                </div>
                                <a href="#!" className="text-body">Forgot password?</a>
                            </div>

                            <div className="text-center text-lg-start mt-4 pt-2">
                                <button type="button" className="btn btn-primary btn-lg" onClick={handleLogin}
                                >Login</button>
                                <p className="small fw-bold mt-2 pt-1 mb-0">Don't have an account? <Link to={"/register"}
                                    className="link-danger">Register</Link></p>
                            </div>

                        </form>
                    </div>
                </div>
            </div>

        </section>
    </>
}

export default Login