import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { addCourse } from '../../public/redux/admin/adminAction'
import { invalidate } from '../../public/redux/admin/adminSlice'

const AddCourse = () => {
    const dispatch = useDispatch()
    const { AddedCourses } = useSelector(state => state.admin)
    const [courseData, setCourseData] = useState({
        coursetype: "30 Days Demo",
        Coursefee: 5000,
        dailyhourse: "2 Hourse Daily",
        vehicle: "alto"
    })
    const handleaddCourse = () => {
        dispatch(addCourse(courseData))
    }

    useEffect(() => {
        if (AddedCourses) {
            setTimeout(() => {
                dispatch(invalidate(null))
            }, 2000);
        }
    }, [AddedCourses])


    return <div className='bg-slate vh-100'>
        <div className="container">
            <div className="row">
                {
                    AddedCourses && <div class="alert alert-primary">
                        {AddedCourses.message}
                    </div>
                }
                <div className="col-sm-6 offset-sm-3 wow fadeInUp" data-wow-delay="0.3s">
                    <div className="courses-item d-flex flex-column bg-white overflow-hidden h-100">
                        <h5 className="mb-3 p-2 text-light text-center fs-3 w-100 bg-warning">
                            <div className='d-flex'>
                                <label className='fs-6' htmlFor="">Add Course Type</label>
                                <input value={courseData.coursetype} onChange={e => setCourseData({ ...courseData, coursetype: e.target.value })} type="text" className='form-control' placeholder='Demo' />
                            </div>
                        </h5>
                        <div className="text-center p-4 pt-0">
                            <div className="d-inline-block  text-slate fontMid my-3">
                                <div className='d-flex'>
                                    <label className='fs-6' htmlFor="">Add Course Fee in RS.6</label>
                                    <input value={courseData.Coursefee} onChange={e => setCourseData({ ...courseData, Coursefee: e.target.value })} type="text" className='form-control' placeholder='Rs.500' />
                                </div>
                            </div>

                            <div className="d-inline-block  text-slate fontMid my-3">
                                <div className='d-flex'>
                                    <label className='fs-6' htmlFor="">Add Course Daily Hour</label>
                                    <input value={courseData.dailyhourse} onChange={e => setCourseData({ ...courseData, dailyhourse: e.target.value })} type="text" className='form-control' placeholder='One hour Daily' />
                                </div>
                            </div>
                            <div className="d-inline-block  text-slate fontMid my-3">
                                <div className='d-flex'>
                                    <label className='fs-6' htmlFor="">Add Course Vehicle</label>
                                    <select value={courseData.vehicle} onChange={e => setCourseData({ ...courseData, vehicle: e.target.value })} class="form-select">
                                        <option value="Alto">Alto</option>
                                        <option value="Wagonar">Wagonar</option>
                                        <option value="Santro">Santro</option>
                                    </select>
                                </div>
                            </div>
                            <div className="  text-slate fontMid my-3">
                                <div>
                                    <button onClick={handleaddCourse} type="button" className="btn btn-primary bg-slate ">Add Course</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
}

export default AddCourse