import { createSlice } from "@reduxjs/toolkit"
import { addCourse, getAllUsers, getCourse } from "./adminAction"
const adminSlice = createSlice({
    name: "admin",
    initialState: {},
    reducers: {
        invalidate: (state, { payload }) => {
            state[payload] = payload
            state.AddedCourses = payload
        }
    },
    extraReducers: builder => {
        builder
            .addCase(getAllUsers.pending, (state, { payload }) => {
                state.loading = true
            })
            .addCase(getAllUsers.fulfilled, (state, { payload }) => {
                state.loading = false
                state.AllUsers = payload
            })
            .addCase(getAllUsers.rejected, (state, { payload }) => {
                state.loading = false
                state.error = payload
            })

            .addCase(addCourse.pending, (state, { payload }) => {
                state.loading = true
            })
            .addCase(addCourse.fulfilled, (state, { payload }) => {
                state.loading = false
                state.AddedCourses = payload
            })
            .addCase(addCourse.rejected, (state, { payload }) => {
                state.loading = false
                state.error = payload
            })

            .addCase(getCourse.pending, (state, { payload }) => {
                state.loading = true
            })
            .addCase(getCourse.fulfilled, (state, { payload }) => {
                state.loading = false
                state.Courses = payload
            })
            .addCase(getCourse.rejected, (state, { payload }) => {
                state.loading = false
                state.error = payload
            })

    }
})
export const { invalidate } = adminSlice.actions
export default adminSlice.reducer