import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { getAllUsers } from '../../public/redux/admin/adminAction'

const ViewUsers = () => {
    const dispatch = useDispatch()
    const { AllUsers, loading, error } = useSelector(state => state.admin)
    const getUsers = () => {
        dispatch(getAllUsers())
    }
    useEffect(() => {
        getUsers()
    }, [])
    return <>
        <div className="container">
            <div className="row">
                {
                    AllUsers && AllUsers.map((item, i) => <div className="col-sm-3">
                        <div className="card">
                            <div className="card-header">{i + 1}</div>
                            <div className="card-body">{item.name}</div>
                            <div className="card-body">{item.email}</div>
                            <div className="card-body">{item.role}</div>
                        </div>
                    </div>
                    )
                }
            </div>
        </div>
    </>
}

export default ViewUsers