import React from 'react'
import carouselOne from "../../assets/image/carouselOne.jpg"
import carouselTwo from "../../assets/image/carouselTwo.jpg"
import "../../assets/css/style.css"
import { Link } from 'react-router-dom'

const Carousel = () => {
    return <>
        <div className="container-fluid p-0 wow fadeIn" data-wow-delay="0.1s">
            <div id="header-carousel" className="carousel slide" data-bs-ride="carousel">
                <div className="carousel-inner">
                    <div className="carousel-item active">
                        <img className="w-100" src={carouselOne} alt="Image" />
                        <div className="carousel-caption">
                            <div className="container">
                                <div className="row justify-content-center">
                                    <div className="col-lg-7">
                                        <h1 className="display-2 text-light mb-5 animated slideInDown familyC" >Learn To Drive With
                                            Confidence</h1>
                                        <Link to={"/"} className="btn rounded-0 text-light btn-warning py-sm-3 px-sm-5">Learn More</Link>
                                        <Link to={"/courses"} className="btn rounded-0 btn-light py-sm-3 px-sm-5 ms-3">Our Courses</Link>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="carousel-item">
                        <img className="w-100" src={carouselTwo} alt="Image" />
                        <div className="carousel-caption">
                            <div className="container">
                                <div className="row justify-content-center">
                                    <div className="col-lg-7">
                                        <h1 className="display-2 text-light mb-5 animated slideInDown familyC" >Safe Driving Is Our Top
                                            Priority</h1>
                                        <Link to={"/"} className="btn btn-warning text-light py-sm-3 px-sm-5 rounded-0">Learn More</Link>
                                        <Link to={"/courses"} className="btn btn-light py-sm-3 px-sm-5 ms-3 rounded-0">Our Courses</Link>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <button className="carousel-control-prev" type="button" data-bs-target="#header-carousel" data-bs-slide="prev">
                    <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span className="visually-hidden">Previous</span>
                </button>
                <button className="carousel-control-next" type="button" data-bs-target="#header-carousel" data-bs-slide="next">
                    <span className="carousel-control-next-icon" aria-hidden="true"></span>
                    <span className="visually-hidden">Next</span>
                </button>
            </div>
        </div>
        <div className="container-fluid facts py-5 pt-lg-0">
            <div className="container py-5 pt-lg-0">
                <div className="row gx-0">
                    <div className="col-lg-4 wow fadeIn" data-wow-delay="0.1s">
                        <div className="bg-white shadow d-flex align-items-center h-100 p-4" >
                            <div className="d-flex">
                                <div className="flex-shrink-0 btn-lg-square bg-warning">
                                    <i className="fa fa-car text-light "></i>
                                </div>
                                <div className="ps-4">
                                    <h5 className='familyC'>Easy Driving Learn </h5>
                                    <span className='familyC'>Clita erat ipsum lorem sit sed stet duo justo erat amet</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-4 wow fadeIn" data-wow-delay="0.3s">
                        <div className="bg-white shadow d-flex align-items-center h-100 p-4" >
                            <div className="d-flex">
                                <div className="flex-shrink-0 btn-lg-square bg-warning">
                                    <i className="fa fa-users text-white"></i>
                                </div>
                                <div className="ps-4">
                                    <h5 className='familyC'>National Instructor</h5>
                                    <span className='familyC'>Clita erat ipsum lorem sit sed stet duo justo erat amet</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-4 wow fadeIn" data-wow-delay="0.5s">
                        <div className="bg-white shadow d-flex align-items-center h-100 p-4" >
                            <div className="d-flex">
                                <div className="flex-shrink-0 btn-lg-square bg-warning">
                                    <i className="fa fa-file-alt text-white"></i>
                                </div>
                                <div className="ps-4">
                                    <h5 className='familyC'>Get licence</h5>
                                    <span className='familyC'>Clita erat ipsum lorem sit sed stet duo justo erat amet</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </>
}

export default Carousel