import React, { useEffect, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import "../../assets/css/style.css"
import { useDispatch, useSelector } from 'react-redux'
import { logout } from '../../public/redux/public/PublicSlice'

const AdminNavbar = () => {
    const navigate = useNavigate()
    const { info } = useSelector(state => state.public)
    const dispatch = useDispatch()
    const [isTrue, setIsTrue] = useState(true)
    useEffect(() => {
        if (isTrue === false) {
            navigate("/")
        }
    }, [isTrue])
    return <>
        <nav className="navbar navbar-expand-sm navbar-light bg-slate navbar-light ">
            <div className="container">

                <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                    <div className="navbar-nav">
                        {
                            info && <div className="dropdown mt-3 me-2">
                                <button className="btn btn-primary dropdown-toggle rounded-circle" type="button" id="dropdownMenuButton1" data-bs-toggle="dropdown" >
                                    {info.name}
                                </button>
                                <ul className="dropdown-menu">
                                    <li><Link className="dropdown-item" to={"/dashboard"}>Dashboard</Link></li>
                                    <li><Link className="dropdown-item " onClick={e => dispatch(logout(), setIsTrue(!isTrue))} >Logout</Link></li>
                                </ul>
                            </div>
                        }
                        <Link className="nav-link adminLink active mx-3 " aria-current="page" to="/admin">Admin Navbar</Link>
                        <Link className="nav-link adminLink text-light" to="/admin/addcourse">Add Courses</Link>
                        <Link className="nav-link adminLink text-light" to="/admin/users">View Users</Link>

                    </div>
                </div>
            </div>
        </nav >
    </>
}

export default AdminNavbar