import React, { useState } from 'react'
import HashLoader from "react-spinners/HashLoader";

const Loader = () => {

    return <div className='mt-5'>
        <div className="sweet-loading text-center">

            <HashLoader
                color='#000'
                loading={true}
                size={80}
            />
        </div>
    </div>
}

export default Loader

